




















import Vue from "vue";

export default Vue.extend({
    name: "RomaConfirmDialog",
    props: {
        value: Boolean,
        title: {
            type: String,
            default: 'Realmente deseas eliminar el item seleccionado?'
        },
        textBtnNo: {
            type: String,
            default: 'No'
        },
        textBtnOk: {
            type: String,
            default: 'Si'
        },
        actionBtnNo: {
            type: Function,
            default () {
                this.toggleShow(false);
            }
        },
        actionBtnOk: {
            type: Function,
            default () {
                this.toggleShow(false);
            }
        },
    },
    data() {
        return {
            show: false,
        };
    },
    methods: {
      toggleShow (value: Boolean) {
          this.$emit('input', value);
      }
    },
});
